import React from 'react';
import { Row, Col, Card, Table } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import IsEmpty from '../../App/components/Reusables/IsEmpty';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import { getGiftCards, deleteGiftCards } from '../../api/payment/giftCards';
import GiftCardsFilter from '../../App/components/SearchFilters/GiftCardsFilter';
import { getGiftCardsSearchFilterObject } from '../../helpers/getSearchFilterObject';

const GiftCards = (props) => {
    const [giftCardsArr, setGiftCardsArr] = React.useState([]);
    const [loading, setLoading] = React.useState(true)
    const [deleteData, setDeleteData] = React.useState({ showModal: false, giftCardUuid: null })
    const [instanceCount, setInstanceCount] = React.useState(0)

    React.useEffect(() => {
        const controller = new AbortController();
        const queryObj = new URLSearchParams(props.location.search);
        const requestJson = {
            "action": "get",
            "filter": "none",
            ...getGiftCardsSearchFilterObject(props),
            "start": parseInt(queryObj.get('start')) || 0,
            "end": (parseInt(queryObj.get('end')) || 25),
        };


        console.log(JSON.stringify(requestJson));
        getGiftCards(requestJson, controller.signal)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    setGiftCardsArr(res.json.gift_cards);
                    setInstanceCount(res.json.count)
                    setLoading(false);
                }
            })
            .catch((err) => {
                console.log(err)
            });
        return () => controller.abort()
    }, []);



    const _triggerDeleteModal = (uuid) => {
        setDeleteData({ showModal: true, giftCardUuid: uuid });
    }

    const _delete = (uuidToBeDeleted) => {
        console.log(uuidToBeDeleted)
        deleteGiftCards({ action: 'delete', gift_cards_uuid: [uuidToBeDeleted] })
            .then(async (res) => {
                console.log(res.json)
                if (res.json.status) {
                    const deletedCard = giftCardsArr.find(gc => gc.uuid === uuidToBeDeleted);
                    let tempArr = giftCardsArr;
                    tempArr.splice(tempArr.indexOf(deletedCard), 1);
                    await setGiftCardsArr([...tempArr])
                    await setDeleteData({ ...setDeleteData, showModal: false, giftCardUuid: null })
                    Jhilikka.fire({ icon: 'success', title: 'Gift Card deleted successfully!', background: '#dffff0' })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch((err) => {
                console.log(err)
            });
    }


    const GiftCardsList = giftCardsArr.map((gc, index) => {
        return (
            <tr key={index}>
                <th scope="row">{gc.id}</th>
                <td
                    onClick={() => {
                        props.history.push('/gift-card/' + gc.uuid)
                    }}
                    style={{ cursor: 'pointer', color: '#55588b' }}>{gc.name}</td>
                <td>{gc.category_str}</td>
                <td>{gc.rate}</td>
                <td>{gc.discount_type}</td>
                <td>{gc.code}</td>
                <td>{gc.is_active ? 'Yes' : 'No'}</td>
                <td>
                    <i
                        className="feather icon-edit text-c-green f-19 m-r-10"
                        style={{ cursor: 'pointer' }}
                        onClick={() => props.history.push('/gift-card/update/' + gc.uuid)}
                    />
                    <i
                        className="feather icon-trash-2 text-c-red f-19 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={() => _triggerDeleteModal(gc.uuid)}
                    />
                </td>
            </tr>
        )
    })

    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Gift Cards</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`gift-card/create`)}
                    > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                </div>
            </Row>
            <Row>
                <Col>

                    <Card>
                        <Card.Body>
                            <GiftCardsFilter />
                            <Table striped responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Name</th>
                                        <th>Category</th>
                                        <th>Rate</th>
                                        <th>Type</th>
                                        <th>Code</th>
                                        <th>Enabled</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {GiftCardsList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(giftCardsArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />
                        </Card.Body>

                    </Card >


                    <DeleteModal
                        show={deleteData.showModal}
                        onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                        onDelete={() => _delete(deleteData.giftCardUuid)}
                    />


                </Col>
            </Row>
        </Aux>
    );
}

export default GiftCards;